//CSS Reset
@use '_reset.scss';
//Font Awesome
@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.1.1/css/all.min.css');
//Google Font
@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;700;800&display=swap');

//Color Variables
$primaryColor: #001524;
$secondaryColor: #ffc300;
$otherColor: #fefefe;

body {
  font-family: 'Open Sans', sans-serif;
  background-color: $primaryColor;
  color: $otherColor;
}

//Header Styles
header {
  .navContainer {
    margin-top: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;

    .formContainer {
      display: flex;
      margin-inline: 1rem;
      width: 100%;
      .searchInput {
        width: 100%;
        border: none;
        border-top-left-radius: 1rem;
        border-bottom-left-radius: 1rem;
        padding: 0.5rem 1rem;
        margin-right: 0;
        outline: none;
      }

      .searchBtn {
        margin: 0;
        width: 10vh;
        border-top-right-radius: 1rem;
        border-bottom-right-radius: 1rem;
        background-color: $secondaryColor;
        color: $primaryColor;
        border: none;
        cursor: pointer;
        outline: none;
      }
    }
  }
  //Trending Container Styles
  .trending {
    display: flex;
    margin-inline: 1rem;
    margin-bottom: 1rem;
    gap: 0.75rem;
    scroll-snap-type: x mandatory;
    overflow: hidden;

    .trendingItem {
      scroll-snap-align: start;
      flex: 0 0 100%;
      max-width: 100%;
      aspect-ratio: 16/9;

      img {
        border-radius: 1rem;
        max-width: 100%;
        margin: 0 auto;
        cursor: pointer;
      }

      h4 {
        text-align: center;
        text-transform: uppercase;
        letter-spacing: 4px;
        font-weight: 400;
        padding-top: 1rem;
      }
    }
  }
}

//Heading For Trending Container
.headingContainer {
  display: flex;
  justify-content: space-between;
  margin: 1rem;
  padding-top: 0.5rem;

  h2 {
    text-transform: uppercase;
    font-weight: 400;
    letter-spacing: 4px;
  }

  //Nav Button Styles
  .btnContainer {
    .leftButton,
    .rightButton {
      background: none;
      border: none;
      color: $otherColor;
      cursor: pointer;
      font-size: 1.5rem;
    }
  }
}

.container {
  max-height: 100vh;
}

.movieItemsContainer {
  display: flex;
  margin-inline: 1rem;
  margin-bottom: 1rem;
  gap: 0.75rem;
  scroll-snap-type: x mandatory;
  overflow: hidden;

  .movieItem {
    scroll-snap-align: start;
    flex: 0 0 100%;
    max-width: 100%;
    aspect-ratio: 16/9;

    img {
      border-radius: 1rem;
      max-width: 100%;
      margin: 0 auto;
      cursor: pointer;
    }

    h4 {
      text-align: center;
      text-transform: uppercase;
      letter-spacing: 4px;
      font-weight: 400;
      padding-top: 1rem;
    }
  }
}
.tvItemsContainer {
  display: flex;
  margin-inline: 1rem;
  gap: 0.75rem;
  scroll-snap-type: x mandatory;
  overflow: hidden;

  &:last-of-type {
    padding-bottom: 1rem;
  }

  .tvItem {
    scroll-snap-align: start;
    flex: 0 0 100%;
    max-width: 100%;
    aspect-ratio: 16/9;

    img {
      border-radius: 1rem;
      max-width: 100%;
      margin: 0 auto;
      cursor: pointer;
    }

    h4 {
      text-align: center;
      text-transform: uppercase;
      letter-spacing: 4px;
      font-weight: 400;
      padding-top: 1rem;
    }
  }
}

